<template>
  <div class="searchRK-box">
    <div class="record-bar">
      <van-nav-bar
        :title="$t('AccountsS')"
        left-arrow
        @click-left="linkHome"
      />
    </div>
    <form action="/">
      <van-search
        v-model="value"
        :placeholder="$t('EnterAccount')"
        show-action
        shape="round"
      >
        <img class="left-icon" slot="left-icon" src="../../assets/homeIcon/searchRK.png" />
        <div slot="action" @click="onSearch">{{$t('search')}}</div>
      </van-search>
    </form>
    <div class="yesContent" v-if="showContent === 1">
      <div class="content">
        <div>
          <div style="color: #666666;">{{$t('account')}}</div>
          <div style="color: #666666;">{{$t('win')}}</div>
          <div style="color: #666666;">{{$t('tradeNum')}}</div>
          <div style="color: #666666;"></div>
        </div>
      </div>
      <div class="content">
        <div>
          <div>{{'***'+(account).substring(account.length-3)}}</div>
          <div style="color: #EC4138">{{winRate}}%</div>
          <div>{{sumCount}}</div>
          <div>
            <div v-if="show" class="follow-but" @click="linkFollow">{{$t('follow')}}</div>
            <div v-else class="follow-but" @click="showNoFollow = true">{{$t('NOF')}}</div>
          </div>
        </div>
      </div>
    </div>
    <div class="noContent" v-if="showContent === 2">
      <div>{{$t('NOA')}}</div>
    </div>
    <van-dialog
      v-model="showNoFollow"
      :title="$t('documentary')"
      show-cancel-button
      confirm-button-color="#fff"
      cancel-button-color="#666666"
      class="noFollow"
      @confirm="confirmNoFollow"
    >
    </van-dialog>
  </div>
</template>

<script>
  import { Toast } from 'vant';
  export default {
    name: "search",
    data(){
      return{
        value:'',
        showContent:0,
        winRate:0,
        account:'******',
        sumCount:0,
        id:'',
        showNoFollow:false,
        show:true
      }
    },
    created(){
      this.uid = localStorage.getItem('uid');
      this.discUrl = localStorage.getItem('discUrl')
      console.log(this.$route.params.account)
    },
    methods:{
      // 取消跟单
      confirmNoFollow(){
        let that = this
        this.$axios.delete('http://'+this.discUrl+':7777/api/documentary/deleteDocumentary?userId='+this.uid).then(res => {
          console.log(res, '取消跟单')
          if(res.data.code === 0){
            Toast({
              duration:2000,
              message:that.$t('noFollowS'),
              position:'bottom',
              onOpened() {
                that.show = true
              }
            })
          }
        })
      },
      onSearch(){
        this.$axios.get('http://'+this.discUrl+':7777/api/documentary/periodWinLostRate?account='+this.value+'&column=winRate&period=week').then(res =>{
          console.log(res, '搜索内容')
          if(res.data.code === 0 && res.data.object !== null){
            this.showContent = 1
            this.winRate = (res.data.object.winRate*100).toFixed(2)
            this.sumCount = res.data.object.sumCount
            this.account = res.data.object.account
            this.id = res.data.object.userId
            this.show = this.account !== this.$route.params.account;
          }else {
            this.showContent = 2
          }
        })
      },
      linkFollow(){
        let that = this
        let postData = {}
        let data1 = {}
        data1.userId = this.uid
        data1.multiple = ''
        data1.direction = ''
        postData.type = 0
        data1.isFollowedId = this.id
        postData.documentary = data1
        this.$axios.post('http://'+this.discUrl+':7777/api/documentary/insertDocumentary',postData).then(res =>{
          console.log(res)
          if(res.data.code === 0){
            let data = {}
            data.account = this.account
            data.winRate = this.winRate
            data.userId = this.id
            data.winRateShow = data.winRate
            this.$router.push({
              name:'follow',
              params:{
                data:data
              }
            })
          }else {
            Toast({
              duration:2000,
              position:'bottom',
              message:res.data.msg
            })
          }
        })
      },
      linkHome(){
        this.$router.push({
          name:'ranking'
        })
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../style/home";
</style>



